import PropTypes from 'prop-types';
import { Component } from 'react';
import { Modal } from '../Modal';
import style from './ImageGalleryItem.module.scss';

export class ImageGalleryItem extends Component {
	static propTypes = {
		array: PropTypes.arrayOf(
			PropTypes.shape({
				id: PropTypes.number.isRequired,
				webformatURL: PropTypes.string.isRequired,
				largeImageURL: PropTypes.string.isRequired,
			}),
		).isRequired,
	};
	state = {
		showModal: false,
	};

	clickOnImage = () => {
		this.setState(({ showModal }) => ({
			showModal: !showModal,
		}));
	};

	render() {
		const { showModal } = this.state;
		const { array } = this.props;
		return array.map(({ id, webformatURL, largeImageURL }) => (
			<li className={style.ImageGalleryItem} key={id}>
				<img src={webformatURL} alt="id" onClick={this.clickOnImage} className={style.Image} />

				{showModal && <Modal alt={id} src={largeImageURL} onClose={this.clickOnImage} />}
			</li>
		));
	}
}
